.nav {

  &__list {
    display: flex;
    padding: 0;
    margin: 0;
  }

  &__list :first-child {
    margin: 0;
  }

  &__item {
    margin-left: 20px;
    height: 60px;
  }

  &__link {
    position: relative;
    display: inline-block;
    padding: 23px 0;

    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-decoration: none;
    text-transform: uppercase;
    color: #000;

    &:hover {
      color: $main-color;
    }
  }

  &__link--active {
    color: $main-color;

    &::after {
      content: "";
      display: block;
      margin: 19px 0;
      width: 100%;
      height: 4px;
      border-radius: 8px;
      background-color: $main-color;
    }
  }
}
